/* MAIN STYLE */

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 3rem 2rem;
}

/* TITLE STYLE */
.header__title {
    font-size: 2.5rem;
}

/* BURGER STYLE */

.header__burger {
    font-size: 2.5rem;
}

/* MODAL STYLE */
.navModal {
    position: absolute;
    top: 7rem;
    right: 2.5rem;
    width: 70%;
}

.navModal__close {
    font-size: 2rem;
    color: rgb(15, 15, 15);
    position: absolute;
    top: 1rem;
    right: 3rem;
}

.navModal ul {
    font-size: 2rem;
    text-align: center;
    color: rgb(80, 80, 80);
    background-color: rgb(240, 240, 240);
    list-style-type: none;
    margin: 0 2rem;
    padding: 3rem 2rem;
    border-radius: 1rem 0 1rem 1rem;
    box-shadow: 0 0 1rem rgb(255, 255, 255);

}

.navModal li {
    margin-bottom: 1rem;
}

.bgBlurr {
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.60);
}

.isActive {
    font-weight: bold;
    border: 1px solid rgba(0, 0, 0, 0);
    border-radius: 100rem;
    box-shadow: .1rem .1rem .5rem rgba(161, 161, 161, 0.555);
    padding: 1rem;
    width: 100%;
}


/* -------------BREAKPOINT---------------- */
@media screen and (min-width: 600px) {
    .header {
        display: block;
        text-align: center;
        border-bottom: 1px solid rgba(212, 212, 212, 0.363);
        padding-bottom: 0;
    }

    .nav__links {
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        list-style-type: none;
    }

    .nav__links__link {
        margin: 3rem 0;
        font-size: 1.2rem;
        color: rgba(255, 255, 255, 0.808);
        cursor: pointer;
        transition: .1s ease;
    }

    .nav__links__link:hover {
        color: white;
        transform: scale(1.2);
    }

    .isActive {
        font-weight: bold;
        border: none;
        border-radius: 0;
        box-shadow: none;
        padding: 1rem;
        width: initial;
        transform: scale(1.2);
        border-bottom: .2rem solid rgb(130, 0, 206);
        color: white;
    }

}

@media screen and (min-width: 1000px) {
    .nav__links__link {
        font-size: 2rem;
    }
}