@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;500&display=swap);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

html {
    font-size: 62.5%;
    color: rgb(240, 240, 240);
}

.App {
    background: url(/planets/static/media/mob_background.657fc8d5.png);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    width: 100vw;
    height: 100%;
}

@media screen and (min-width: 600px) {
    .App {
        background: url(/planets/static/media/desk_background.20e4a189.png);
        min-height: 100vh;
        background-repeat: no-repeat;
        background-size: cover;
        overflow-x: hidden;
    }
}
/* MAIN STYLE */

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 3rem 2rem;
}

/* TITLE STYLE */
.header__title {
    font-size: 2.5rem;
}

/* BURGER STYLE */

.header__burger {
    font-size: 2.5rem;
}

/* MODAL STYLE */
.navModal {
    position: absolute;
    top: 7rem;
    right: 2.5rem;
    width: 70%;
}

.navModal__close {
    font-size: 2rem;
    color: rgb(15, 15, 15);
    position: absolute;
    top: 1rem;
    right: 3rem;
}

.navModal ul {
    font-size: 2rem;
    text-align: center;
    color: rgb(80, 80, 80);
    background-color: rgb(240, 240, 240);
    list-style-type: none;
    margin: 0 2rem;
    padding: 3rem 2rem;
    border-radius: 1rem 0 1rem 1rem;
    box-shadow: 0 0 1rem rgb(255, 255, 255);

}

.navModal li {
    margin-bottom: 1rem;
}

.bgBlurr {
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.60);
}

.isActive {
    font-weight: bold;
    border: 1px solid rgba(0, 0, 0, 0);
    border-radius: 100rem;
    box-shadow: .1rem .1rem .5rem rgba(161, 161, 161, 0.555);
    padding: 1rem;
    width: 100%;
}


/* -------------BREAKPOINT---------------- */
@media screen and (min-width: 600px) {
    .header {
        display: block;
        text-align: center;
        border-bottom: 1px solid rgba(212, 212, 212, 0.363);
        padding-bottom: 0;
    }

    .nav__links {
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        list-style-type: none;
    }

    .nav__links__link {
        margin: 3rem 0;
        font-size: 1.2rem;
        color: rgba(255, 255, 255, 0.808);
        cursor: pointer;
        transition: .1s ease;
    }

    .nav__links__link:hover {
        color: white;
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
    }

    .isActive {
        font-weight: bold;
        border: none;
        border-radius: 0;
        box-shadow: none;
        padding: 1rem;
        width: auto;
        width: initial;
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
        border-bottom: .2rem solid rgb(130, 0, 206);
        color: white;
    }

}

@media screen and (min-width: 1000px) {
    .nav__links__link {
        font-size: 2rem;
    }
}
/* GENERAL STYLE */
.main {
    padding: 2rem;
    text-align: center;
}

/* BUTTONS STYLE */
.buttons {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    padding-top: 1rem;
    border-top: 1px solid rgba(141, 141, 141, 0.466);
    border-bottom: 1px solid rgba(141, 141, 141, 0.466);
}

.buttons__btn {
    color: rgb(219, 219, 219);
    padding-bottom: .5rem;
}

.btnIsActive {
    border-bottom: .5rem solid rgb(130, 0, 206);
}

/* IMG STYLE */
.planet {
    margin: 6rem 0;
}

/* COPY STYLE */
.copy__title {
    font-size: 3rem;
    margin-bottom: 1.5rem;
}

.copy__content {
    color: rgb(223, 223, 223);
    font-size: 1.3rem;
    font-weight: 100;
    line-height: 2.2rem;
    margin-bottom: 1.5rem;
}

.copy__link {
    font-size: 1.3rem;
    font-weight: 100;
    color: rgb(143, 143, 143);
    margin-bottom: 2.5rem;
}

.copy__link__wiki {
    color: rgb(223, 223, 223);
    font-weight: 100;
}

.copy__link__icon {
    margin-left: .5rem;
}

/* INFO STYLE */
.infos {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 1px solid rgba(192, 192, 192, 0.466);
    padding: 1rem 2rem;
    margin-bottom: 1.2rem;
    border-radius: .1rem;
}

.infos__title {
    color: rgb(192, 192, 192);
    font-weight: 900;
}

.infos__num {
    font-size: 2rem;
}



/* -------------BREAKPOINT---------------- */
@media screen and (min-width: 600px) {

    .main {
        padding: 0 4rem;
        max-width: 115rem;
        margin: 0 auto;
    }

    /* GENERAL COPY */
    .copy {
        text-align: left;
        display: grid;
        align-items: center;
        grid-template-columns: 1fr 1fr;
        grid-gap: 5rem;
        gap: 5rem;
        margin-bottom: 3rem;
    }

    /* COPY LEFT */
    .copy__title {
        font-size: 4rem;
    }

    .copy__content {
        font-size: 1.7rem;
        line-height: 2.5rem;
    }

    /* COPY RIGHT */
    .buttons {
        display: block;
        border: none;
        width: 100%;
    }

    .buttons__btn {
        border: 1px solid white;
        margin-bottom: 2rem;
        padding: 2rem;
        cursor: pointer;
    }

    .buttons__btn:hover {
        background-color: rgb(130, 0, 206);
    }

    .btnIsActive {
        background-color: rgb(130, 0, 206);
    }

    /* ADDITIONAL INFO */
    .infos__container {
        display: flex;
        justify-content: space-between;
    }

    .infos {
        display: block;
        text-align: left;
        padding: 3rem;
        /* width: 20rem; */
    }
}
