/* GENERAL STYLE */
.main {
    padding: 2rem;
    text-align: center;
}

/* BUTTONS STYLE */
.buttons {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    padding-top: 1rem;
    border-top: 1px solid rgba(141, 141, 141, 0.466);
    border-bottom: 1px solid rgba(141, 141, 141, 0.466);
}

.buttons__btn {
    color: rgb(219, 219, 219);
    padding-bottom: .5rem;
}

.btnIsActive {
    border-bottom: .5rem solid rgb(130, 0, 206);
}

/* IMG STYLE */
.planet {
    margin: 6rem 0;
}

/* COPY STYLE */
.copy__title {
    font-size: 3rem;
    margin-bottom: 1.5rem;
}

.copy__content {
    color: rgb(223, 223, 223);
    font-size: 1.3rem;
    font-weight: 100;
    line-height: 2.2rem;
    margin-bottom: 1.5rem;
}

.copy__link {
    font-size: 1.3rem;
    font-weight: 100;
    color: rgb(143, 143, 143);
    margin-bottom: 2.5rem;
}

.copy__link__wiki {
    color: rgb(223, 223, 223);
    font-weight: 100;
}

.copy__link__icon {
    margin-left: .5rem;
}

/* INFO STYLE */
.infos {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 1px solid rgba(192, 192, 192, 0.466);
    padding: 1rem 2rem;
    margin-bottom: 1.2rem;
    border-radius: .1rem;
}

.infos__title {
    color: rgb(192, 192, 192);
    font-weight: 900;
}

.infos__num {
    font-size: 2rem;
}



/* -------------BREAKPOINT---------------- */
@media screen and (min-width: 600px) {

    .main {
        padding: 0 4rem;
        max-width: 115rem;
        margin: 0 auto;
    }

    /* GENERAL COPY */
    .copy {
        text-align: left;
        display: grid;
        align-items: center;
        grid-template-columns: 1fr 1fr;
        gap: 5rem;
        margin-bottom: 3rem;
    }

    /* COPY LEFT */
    .copy__title {
        font-size: 4rem;
    }

    .copy__content {
        font-size: 1.7rem;
        line-height: 2.5rem;
    }

    /* COPY RIGHT */
    .buttons {
        display: block;
        border: none;
        width: 100%;
    }

    .buttons__btn {
        border: 1px solid white;
        margin-bottom: 2rem;
        padding: 2rem;
        cursor: pointer;
    }

    .buttons__btn:hover {
        background-color: rgb(130, 0, 206);
    }

    .btnIsActive {
        background-color: rgb(130, 0, 206);
    }

    /* ADDITIONAL INFO */
    .infos__container {
        display: flex;
        justify-content: space-between;
    }

    .infos {
        display: block;
        text-align: left;
        padding: 3rem;
        /* width: 20rem; */
    }
}